<template>
<example-attack :my-list="menuAttack">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم یوونتوس</p>
  </template>
</example-attack>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AttackYouv",
  components: {ExampleAttack},
  setup(){
    const menuAttack=reactive([])
    const showAttack=onMounted(async()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/786'),
        axios.get('https://api.footballi.net/api/v2/player/1481'),
        axios.get('https://api.footballi.net/api/v2/player/1897'),
        axios.get('https://api.footballi.net/api/v2/player/2322'),
        axios.get('https://api.footballi.net/api/v2/player/3719'),
        axios.get('https://api.footballi.net/api/v2/player/6625'),
        axios.get('https://api.footballi.net/api/v2/player/2258118'),
        axios.get('https://api.footballi.net/api/v2/player/2272895'),
        axios.get('https://api.footballi.net/api/v2/player/2289131'),
        axios.get('https://api.footballi.net/api/v2/player/2444648'),
      ])
      .then(axios.spread((...response)=>{
        menuAttack.push(response)
      }))
      .catch((error)=>{
      handleError(error)
      })
    })
    return{
      menuAttack,showAttack
    }
  }
}
</script>

