<template>
<example-super-goal :my-list="list">

</example-super-goal>
</template>

<script>
import {reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleSuperGoal from "@/components/main/ExampleSuperGoal";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "SuperGoalYouv",
  components: {ExampleSuperGoal},
  setup(){
    const list=reactive([])
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/41775'),
        axios.get('https://api.footballi.net/api/v2/player/44901'),
        axios.get('https://api.footballi.net/api/v2/player/132'),
        axios.get('https://api.footballi.net/api/v2/player/1481'),
      ])
      .then(axios.spread((...response)=>{
        list.push(response)
      }))
      .catch((error)=>{
      handleError(error)
      })
    })
    return{
      list,total
    }
  }
}
</script>

