<template>
  <example-transfer :my-list="playersTrans">

  </example-transfer>
</template>

<script>
import{reactive,onMounted} from 'vue'
import axios from 'axios'
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TransferTeamYouv",
  components: {ExampleTransfer},
  setup(){
    const playersTrans=reactive([])
    const sum=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/2257529'),
        axios.get('https://api.footballi.net/api/v2/player/2225320'),
        axios.get('https://api.footballi.net/api/v2/player/2222544'),
        axios.get('https://api.footballi.net/api/v2/player/2362672'),
        axios.get('https://api.footballi.net/api/v2/player/2340812'),
        axios.get('https://api.footballi.net/api/v2/player/12430'),
        axios.get('https://api.footballi.net/api/v2/player/2272895'),
        axios.get('https://api.footballi.net/api/v2/player/2340812'),
        axios.get('https://api.footballi.net/api/v2/player/44901'),
        axios.get('https://api.footballi.net/api/v2/player/2396421'),
        axios.get('https://api.footballi.net/api/v2/player/709'),
        axios.get('https://api.footballi.net/api/v2/player/14044'),
        axios.get('https://api.footballi.net/api/v2/player/2275387'),
        axios.get('https://api.footballi.net/api/v2/player/6620'),
        axios.get('https://api.footballi.net/api/v2/player/5601'),
      ])
      .then(axios.spread((...response)=>{
        playersTrans.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      playersTrans,sum
    }
  }
}
</script>

