<template>
<example-defender :my-list="myDef">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم یونتوس</p>
  </template>
</example-defender>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleDefender from "@/components/main/ExampleDefender";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "DefenderYouv",
  components: {ExampleDefender},
  setup(){
    const myDef=reactive([])
    const showDef=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/781'),
        axios.get('https://api.footballi.net/api/v2/player/856'),
        axios.get('https://api.footballi.net/api/v2/player/1772'),
        axios.get('https://api.footballi.net/api/v2/player/1803'),
        axios.get('https://api.footballi.net/api/v2/player/2226880'),
        axios.get('https://api.footballi.net/api/v2/player/2258523'),
        axios.get('https://api.footballi.net/api/v2/player/2439257'),
      ])
      .then(axios.spread((...response)=>{
        myDef.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      myDef,showDef
    }
  }
}
</script>

