<template>
<example-team-rank :my-list="myList">
  <template #rankName>
    <option class="font-bold text-slate-900 text-sm text-start">لیگ برتر ایتالیا</option>
  </template>
</example-team-rank>
</template>

<script>
import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import ExampleTeamRank from "@/components/main/ExampleTeamRank";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "RankYouv",
  components:{ExampleTeamRank},
  setup(){
    const myList=reactive([])
    const sum=onMounted(()=>{
      EndPoints.getDescriptionYouv()
      .then((response)=>{
        if(response.data.success){
          myList.push(response.data.data.standing)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      myList,sum
    }
  }
}
</script>

