<template>
<example-team-news :my-list="listNews">

</example-team-news>
</template>

<script>
import{reactive,onMounted} from 'vue'
import EndPoints from "@/service/EndPoints";
import ExampleTeamNews from "@/components/main/ExampleTeamNews";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "LastNewsYouv",
  components: {ExampleTeamNews},
  setup(){
    const listNews=reactive([])
    const total=onMounted(()=>{
      EndPoints.getNewsYouv()
          .then((response)=>{
            if(response.data.success){
              listNews.push(response.data.data)
            }
          })
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      listNews,total
    }
  }
}
</script>

