<template>
<section class="center-players">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <p class="font-semibold text-start text-black w-full">بازیکنان تیم یوونتوس</p>
      <div class="bg-black h-0.5 w-full my-1"></div>
      <div class="bg-green-500 w-full min-h-full relative px-3">
        <example-goal-keeper :my-list="menu">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم یوونتوس</p>
          </template>
        </example-goal-keeper>
        <defender-youv></defender-youv>
        <attack-youv></attack-youv>
        <main-attack></main-attack>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>
    </div>
  </div>
</section>
</template>

<script>
import{reactive,onMounted} from 'vue'
import axios from 'axios'
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
import DefenderYouv from "@/components/main/TeamYouv/DefenderYouv";
import AttackYouv from "@/components/main/TeamYouv/AttackYouv";
import MainAttack from "@/components/main/TeamEsteghlal/MainAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "PlayersYouv",
  components: {MainAttack, AttackYouv, DefenderYouv, ExampleGoalKeeper},
  setup(){
    const menu=reactive([])
    const sum=onMounted(async()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/116'),
        axios.get('https://api.footballi.net/api/v2/player/1991'),
        axios.get('https://api.footballi.net/api/v2/player/2225763'),
      ])
      .then(axios.spread((...response)=>{
        menu.push(response)
      }))
      .catch((error)=>{
      handleError(error)
      })
    })
    return{
      menu,sum
    }
  }
}
</script>

